
import { COLORS, industryMixin, YEAR } from '~/libs/netZeroUtils'

const DEFAULT_Y_MAX = 150

export default {
  mixins: [industryMixin],
  async asyncData ({ $content, params, redirect }) {
    let bauStats = []

    bauStats = await $content('overview/bau').fetch()
    bauStats = bauStats.body.map((row) => {
      return {
        ...row,
        年份: row.年份 - 0,
        Tot數值: row.Tot數值 - 0,
        Tot變化: row.Tot變化 - 0
      }
    })

    const top5Industries = bauStats
      .filter(row => row.年份 === YEAR.BASE)
      .sort((a, b) => b.Tot數值 - a.Tot數值)
      .slice(0, 5)
      .reduce((ret, row) => {
        ret[row.產業別] = row
        return ret
      }, {})

    bauStats = bauStats.filter(row => top5Industries[row.產業別])

    return { bauStats, top5IndustryCodes: Object.keys(top5Industries) }
  },
  computed: {
    industry () {
      return this.industries[0]
    },
    top5Industries () {
      return this.top5IndustryCodes.map((code) => {
        return this.industryMap[code]
      })
    },
    industryMap () {
      return this.industries.reduce((sum, industry) => {
        sum[industry.code] = industry
        return sum
      }, {})
    },
    yMax () {
      return this.bauStats.reduce((max, row) => {
        if (row.Tot變化 > max) {
          return row.Tot變化
        }
        return max
      }, DEFAULT_Y_MAX)
    },
    chartColors () {
      return COLORS
    }
  },
  beforeDestroy () {
    // #109, when switch page, html scrollbar disabler is not reset for some reason
    const html = document.querySelector('html')
    if (html.classList.contains('is-clipped-touch')) {
      html.classList.remove('is-clipped-touch')
    }
  },
  methods: {
    industryLink (industry) {
      return {
        name: this.$route.name,
        params: {
          industry
        },
        query: this.$route.query
      }
    }
  }
}
